@import "../../utils/dataStyle.module";

.product {
  font-family: 'El Messiri', sans-serif;
  display: inline-block;
  overflow: hidden;
  border-radius: 4px;
  color: white;
  transition: .2s;
  box-shadow: $boxShadowLight;

  .box {
    height: 100%;
    width: 100%;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    background: linear-gradient(to bottom, $light 40%, $hard);

    .buttons {
      display: flex;
      width: 100%;

      button {
        color: white;
        font-size: 14px;
        padding: 14px 0;
        cursor: pointer;
        border: none;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        min-width: 50%;
        border-top: 1px solid rgba(255,255,255,.7);
        transition: .2s;
        background: transparent;

        i {
          font-size: 18px;
          margin-right: 3px; }

        &:hover {
          transition: .2s;
          background: rgba(255,255,255,.4); }

        &:first-child {
          border-top-right-radius: 14px;
          border-right: 1px solid $light; }

        &:last-child {
          border-top-left-radius: 14px;
          border-left: 1px solid $light; } }

      .isBasket {
        justify-content: center;

        i {
          font-size: 22px; } } }

    .subBox {
      height: 100%;
      display: flex;
      flex-direction: column;
      justify-content: space-between;

      .counter {
        font-size: 14px;
        padding: 5px;

        .titleCount {
          padding-bottom: 3px;

          .tit {
            background: none!important;
            padding-left: 0!important;
            margin: 0!important; }

          span {

            &:last-child {
              padding: 0 5px;
              background: rgba(255,255,255,.3);
              border-radius: 4px;
              margin-left: 3px; } } }

        input {
          font-size: 16px;
          border: 1px solid #a1a1a1;
          border-radius: 4px;
          padding-left: 5px;
          box-shadow: $bsBasket; }

        .invalid {
          transition: .2s;
          border-color: red; } }

      .imgBox {
        padding: 2px;
        cursor: pointer;

        img {
          height: auto;
          max-width: 100%;
          box-shadow: 0 2px 0 0 rgba(0,0,0,.2); } }

      .descr {
        width: 100%;
        padding: 10px 3px;
        padding-bottom: 0;
        margin-bottom: auto;

        .name {
          font-size: 18px;
          text-transform: capitalize;
          text-align: center;
          margin-bottom: 10px; }

        .sizes {
          font-size: 15px;
          text-align: left;
          display: flex;
          flex-direction: column;

          .size {
            height: 30px;
            padding-top: 5px;
            padding-left: 5px;
            margin-top: 3px;
            min-width: 30px;
            margin-left: 3px;
            background: rgba(255,255,255,.3);
            margin-right: 1px;
            border-radius: 4px;
            cursor: pointer;
            transition: .2s;
            color: white; }

          .activeSize {
            transition: .2s;
            color: $hard;
            border: 1px solid rgba(255,255,255,.7);
            box-shadow: $bsBasket;
 } } }            //background: rgba(255,255,255,.3)

      .price {
        padding-left: 3px;
        font-size: 15px;
        display: flex;
        padding-top: 4px;
        padding-bottom: 4px;
        align-items: center;
        background: rgba(255,255,255,.3);
        border-radius: 4px;
        box-shadow: $bsBasket;
        margin: 0 20px;
        margin-bottom: 10px;
        justify-content: center;
        color: black;

        span {
          margin-right: 3px;
          text-align: left; } } } } }


.productThem {
  font-family: 'El Messiri', sans-serif;
  display: inline-block;
  overflow: hidden;
  border-radius: 4px;
  color: white;
  transition: .2s;
  box-shadow: $boxShadowLight;

  .box {

    .buttons {
      display: flex;
      justify-content: space-between;
      padding: 5px;

      button {
        padding: 10px 0;
        border: 2px solid $hard;
        border-radius: 4px;
        background-color: white;


        i {
          margin-right: 3px; }

        &:hover {
          transition: .2s;
          background-color: $hard;
          color: white; }

        &:first-child {
          width: 39%; }

        &:last-child {
          width: 59%; } }

      .isBasket {

        i {} } }

    .subBox {

      .imgBox {
        box-shadow: $bsBasket;
        position: relative;
        z-index: 5;

        img {
          height: auto;
          max-width: 100%; } }

      .descr {
        padding: 0 5px;

        .name {
          position: relative;
          z-index: 2;
          border-bottom-left-radius: 4px;
          border-bottom-right-radius: 4px;
          background: $hard;
          padding: 5px 0;
          box-shadow: $bsBasket; }

        .sizes {
          color: black;
          text-align: left;
          display: flex;
          align-items: center;
          flex-wrap: wrap;

          .titleSize {
            margin-right: 3px; }

          .size {
            cursor: pointer;
            display: flex;
            align-items: center;
            justify-content: center;
            padding-top: 3px;
            border-radius: 4px;
            height: 35px;
            width: 40px;
            margin-right: 3px;
            transition: .2s;
            border: 1px solid #a1a1a1; }

          .activeSize {
            transition: .2s;
            border-color: $hard;
            box-shadow: $bsBasket; } } }

      .price {
        color: black;
        display: flex;
        padding: 5px;

        span {
          margin-right: 3px; } } } } }


.titleForm {

  .titleFormPrice {
    color: white;
    display: block;
    font-size: 32px;
    font-weight: 800;

    small {
      margin-left: 3px;
      font-weight: 800; } } }

@import "./media";
