@import "../../utils/dataStyle.module";

.delivery {
  font-family: 'El Messiri', sans-serif;
  padding-bottom: 30px;

  .cards {
    display: flex;
    justify-content: center;

    .card {
      background-image: url("../../public/images/rose3.jpg");
      background-repeat: no-repeat;
      background-size: cover;
      background-position: bottom left;
      width: 100%;
      padding: 5px;
      min-height: 200px;
      border-radius: 4px;
      display: flex;
      flex-direction: column;
      align-items: center;
      position: relative;
      box-shadow: $boxShadowLight;
      z-index: 5;

      &:before {
        position: absolute;
        content: '';
        top: 0;
        right: 0;
        left: 0;
        bottom: 0;
        background-color: rgba(255,255,255,.7);
        z-index: -2; }

      .index {
        color: $hard;
        font-size: 20px;
        z-index: 6; }

      img {
        position: absolute;
        bottom: 3px;
        right: 50%;
        transform: translateX(50%);
        height: auto;
        max-width: 70px;
        z-index: -3; }

      .name {
        text-transform: uppercase;
        font-weight: 600;
        font-size: 16px;
        text-align: center;

        span {
          color: black; } } } } }

@import "./media";
